.App {
    font-family: 'Noto Sans KR';
  }
  
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  li {
    list-style-type: none;
  }
  
  a {
    color: #fff;
    text-decoration: none;
  }
  
  nav {
    display: flex;
    justify-content: space-around;
    align-items: center;
    min-height: 10vh;
    font-family: 'Noto Sans KR';
    background-color: #000423;
    color: white;
  }
  
  nav .logo {
    font-size: 24px;
    font-weight: 700;
    text-transform: uppercase;
  }
  
  nav .nav-links {
    display: flex;
    justify-content: space-around;
    width: 35%;
  }
  
  nav .nav-links a {
    font-size: 18px;
    cursor: pointer;
  }
  
  nav .nav-links a:hover {
    border-bottom: 1px solid white;
  }

  nav .nav-links #login {
    font-size: 18px;
    cursor: pointer;
    background: #268FE1;
  }

  nav .burger {
    font-size: 30px;
    display: none;
    cursor: pointer;
  }
  
  @media all and (max-width: 768px) {
    nav .burger {
      display: block;
    }
  
    nav .nav-links {
      position: absolute;
      display: block;
      top: 0;
      left: 0;
      height: 100vh;
      width: 50%;
      background: black;
      /* padding: 40px; */
      text-align: center;
      -webkit-transform: translateX(-500px);
              transform: translateX(-500px);
      transition: 0.5s ease-in-out;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  
    nav .nav-links li {
      padding: 25px;
      /* border-bottom: 1px solid white; */
    }
  
    /* nav .nav-links li:last-of-type {
      border-bottom: 0px solid white;
    } */
  
    nav .nav-links a:hover {
      border-bottom: 0px solid white;
    }
  }
  
